import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import Hero from './components/Hero';
import BubbleAnnouncement from './components/BubbleAnnouncement';
import BubbleStatus from './components/BubbleStatus';
import Row from './components/Row';
import About from './components/About';
import Features from './components/Features';
import Footer from './components/Footer';
import MailChimpForm from './components/MailChimpForm';

const Content = styled.div`
  max-width: 820px;
  margin: 0 auto;
  padding: 0 15px;

  & hr {
    margin: 20px 0;
    border: 1px solid #1b1b1b;
  }
`;

function App() {
  const [formVisible, setFormVisible] = useState(false);
  const onOpenForm = () => {
    window.location.hash = '#subscribe';
    setFormVisible(true);
  };
  const onCloseForm = () => {
    window.location.hash = '';
    setFormVisible(false);
  };

  useEffect(() => {
    const initialVisible = window.location.hash === '#subscribe';
    initialVisible && onOpenForm();
  }, []);

  return (
    <>
      <Hero onSubscribe={onOpenForm} />
      <Content>
        <Row>
          <BubbleAnnouncement />
          <BubbleStatus />
        </Row>
        <hr />
        <About />
        <hr />
        <Features />
      </Content>
      <Footer />
      <MailChimpForm visible={formVisible} onClose={onCloseForm} />
    </>
  );
}

export default App;
